ul{
    display: flex;
    flex-direction: column;
}
li{
    list-style:none;
    margin: 7px 0 ;
    cursor: pointer;
}
li:hover{
    background: rgb(221, 221, 221);
}

#correct{
    order: 1;
    color: red;
}
.end-Container{
    margin-top: 40px;
    width: 50vw;
    height: fit-content;
    min-width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 60px;
    border: 1px solid black;
    font-size: 36px;
    background-color: #fff;
    color: black;
    animation: end_cont 3s ;
}  
.Homepage-Container{
    animation: end_cont 2s ease-in-out ;
}
@keyframes end_cont{
    0%{
        opacity: 0.1;
    }
    100%{
        opacity: 1;
    }
}
.circle{
    margin-top: 10px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media (max-width:770px){
    .end-Container{
        font-size: 26px;
    }
}

.bouncer{
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    width: 100px;
    height: 100px;
}

.ball{
    width: 20px;
    height: 20px;
    background-color: rgb(4, 207, 4);
    border-radius: 50%;
    animation: bouncer 0.5s cubic-bezier(.19,.57,.3,.98) infinite alternate ;
}
.bouncer div:nth-child(2){
    animation-delay: 0.1s;
    opacity: 0.9;
}

.bouncer div:nth-child(3){
    animation-delay: 0.2s;
    opacity: 0.8;
}
.bouncer div:nth-child(4){
    animation-delay: 0.3s;
    opacity: 0.7;
}

@keyframes bouncer{
    from {transform: translateY(0)}
    to {transform: translateY(-100px)}
}