.Homepage-Container{
    margin-top: 40px;
    width: 50vw;
    height: fit-content;
    min-width: 300px;
    text-align: left;
    padding: 60px;
    border: 1px solid black;
    font-size: 26px;
    background-color: #fff;
    color: black;
}  
.Category-Container{
    width: 100%;
    text-align: left;
    padding: 10px 0;
    font-size: 24px;
}
select{
    width: 33%;
    outline: none;
    border: 1px solid rgba(0,0,0,.363);
    cursor: pointer;
    background-color: #fff;
    padding: 3px;
    margin: 5px 0 ;
    font-size: 22px;
    
}
.start-btn{
    margin-top: 30px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 30%;
    min-width: fit-content;
    height: fit-content;
    padding: 5px 20px;
    font-size: 32px;
    outline: none;
    background:#0f9d58;
    border: none;
    cursor: pointer;
    color: #fff;
}
.start-btn:hover{
    opacity: .8;
}
.start-link{
    text-decoration: none;
    color: #fff;
}
@media (max-width:770px){
    .Homepage-Container{
        font-size: 16px;
        padding: 30px;
    }
    select{
        width: 150px;
    }
}